import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import ServiceList from '../components/common/ServiceList';
import { PageLoading } from '../components/common/LoadingSpinner';
import { toast } from '../components/common/Toast';
import AppLayout from '../components/Layout/AppLayout';
import apiClient from '../services/apiClient';

const Content = styled.div`
  padding: var(--spacing-4);
`;

// key
const ViewPreferencesKey = 'service_view_preferences';

/**
 * 
 */
const ServicesPage = () => {
  const [services, setServices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  // 
  const loadViewPreferences = () => {
    try {
      const saved = localStorage.getItem(ViewPreferencesKey);
      if (saved) {
        return JSON.parse(saved);
      }
    } catch (err) {
      console.error(':', err);
    }

    // 
    return {
      viewMode: 'grid',
      sortBy: 'newest'
    };
  };

  // 
  const saveViewPreferences = (preferences) => {
    try {
      localStorage.setItem(ViewPreferencesKey, JSON.stringify(preferences));
    } catch (err) {
      console.error(':', err);
    }
  };

  // 
  const [viewPreferences, setViewPreferences] = useState(loadViewPreferences());

  // 
  const updateViewPreferences = (updates) => {
    const newPreferences = { ...viewPreferences, ...updates };
    setViewPreferences(newPreferences);
    saveViewPreferences(newPreferences);
  };

  // 
  const loadServices = async () => {
    setLoading(true);
    setError(null);

    try {
      const result = await apiClient.getItems();
      const items = result.items || [];

      // 
      const mappedServices = items.map(item => ({
        id: item.id || item.tool_id,
        title: item.name || item.title,
        description: item.description,
        tags: item.tags || [],
        type: item.type || 'default',
        provider: item.provider || '',
        createdAt: item.created_at || new Date().toISOString(),
        rating: item.rating || (Math.random() * 2 + 3).toFixed(1) // 
      }));

      setServices(mappedServices);
    } catch (err) {
      console.error(':', err);
      setError('');
      toast.error('');
    } finally {
      setLoading(false);
    }
  };

  // 
  const handleServiceClick = (serviceId) => {
    // 
    navigate(`/services/${serviceId}`);
  };

  // 
  const handleViewModeChange = (mode) => {
    updateViewPreferences({ viewMode: mode });
  };

  // 
  useEffect(() => {
    loadServices();
  }, []);

  return (
    <AppLayout
      title=""
      showTabBar={true}
      showSideNav={true}
      showBack={true}
      onBack={() => navigate('/')}
    >
      <ServiceList
        services={services}
        loading={loading}
        error={error}
        onServiceClick={handleServiceClick}
        onRetry={loadServices}
        defaultView={viewPreferences.viewMode}
        onViewModeChange={handleViewModeChange}
      />
    </AppLayout>
  );
};

export default ServicesPage; 