import React, { createContext, useState, useEffect, useCallback } from 'react';
import apiClient from '../services/apiClient';
import { toast } from '../components/common/Toast';

// 
export const AuthContext = createContext({
  isAuthenticated: false,
  user: null,
  token: null,
  loading: false,
  error: null,
  role: null,
  login: () => { },
  register: () => { },
  logout: () => { },
  clearError: () => { }
});

// 
export const AuthProvider = ({ children }) => {
  //  localStorage token 
  const [user, setUser] = useState(() => {
    try {
      const storedUser = localStorage.getItem('user');
      return storedUser ? JSON.parse(storedUser) : null;
    } catch (e) {
      console.error('', e);
      return null;
    }
  });

  const [token, setToken] = useState(() => localStorage.getItem('token'));

  //  token  checkAuth 
  const [isAuthenticated, setIsAuthenticated] = useState(() => !!localStorage.getItem('token'));

  const [loading, setLoading] = useState(true); // true
  const [error, setError] = useState(null);
  const [role, setRole] = useState(() => localStorage.getItem('userRole') || (user?.role || 'user'));

  // 
  const clearError = () => setError(null);

  // 
  const setAuth = (userData, authToken, userRole) => {
    localStorage.setItem('token', authToken);
    if (userRole) {
      localStorage.setItem('userRole', userRole);
    }
    setToken(authToken);
    setUser(userData);
    // 
    try {
      localStorage.setItem('user', JSON.stringify(userData));
    } catch (e) {
      console.warn(' localStorage', e);
    }
    setRole(userRole || 'user');
    setIsAuthenticated(true);
    setLoading(false);
    apiClient.setAuthToken(authToken);
  };

  // 
  const clearAuth = useCallback(() => {
    localStorage.removeItem('token');
    localStorage.removeItem('userRole');
    localStorage.removeItem('user');
    setToken(null);
    setUser(null);
    setRole('user');
    setIsAuthenticated(false);
    setLoading(false);
    apiClient.setAuthToken(null);
  }, []);

  // 
  const login = async (username, password) => {
    setLoading(true);
    setError(null);

    try {
      console.log('...');
      const response = await apiClient.login(username, password);
      console.log(':', response);

      if (response.success) {
        console.log('...');
        setAuth(response.user, response.token, response.user?.role);
        toast.success('');
        console.log('isAuthenticatedtrue');
        return true;
      } else {
        console.log(':', response.message);
        setError(response.message || '');
        setLoading(false);
        toast.error(response.message || '');
        return false;
      }
    } catch (err) {
      console.error(':', err);
      const errorMessage = err.response?.data?.message || err.message || '';
      setError(errorMessage);
      setLoading(false);
      toast.error(errorMessage);
      return false;
    }
  };

  // 
  const register = async (username, password, email) => {
    setLoading(true);
    setError(null);

    try {
      const response = await apiClient.register(username, password, email);

      if (response.success) {
        setAuth(response.user, response.token, response.user?.role);
        toast.success('');
        return true;
      } else {
        setError(response.message || '');
        setLoading(false);
        toast.error(response.message || '');
        return false;
      }
    } catch (err) {
      const errorMessage = err.response?.data?.message || '';
      setError(errorMessage);
      setLoading(false);
      toast.error(errorMessage);
      return false;
    }
  };

  // 
  const logout = () => {
    clearAuth();
    toast.success('');
  };

  // token
  const checkAuth = useCallback(async () => {
    const storedToken = localStorage.getItem('token');
    const storedUserRaw = localStorage.getItem('user');

    if (!storedToken) {
      //  token
      setLoading(false);
      setIsAuthenticated(false);
      return;
    }

    // 
    if (storedUserRaw) {
      try {
        const cachedUser = JSON.parse(storedUserRaw);
        setUser(cachedUser);
        setRole(cachedUser?.role || 'user');
        setIsAuthenticated(true);
      } catch (e) {
        console.warn('', e);
      }
    }

    try {
      // APItoken
      apiClient.setAuthToken(storedToken);

      //  token 
      const userData = await apiClient.getUserInfo();

      if (userData.success) {
        setUser(userData.user);
        setRole(userData.user?.role || 'user');
        setIsAuthenticated(true);
        setToken(storedToken);
        // 
        localStorage.setItem('user', JSON.stringify(userData.user));
      } else {
        // 
        if (!storedUserRaw) {
          clearAuth();
        }
      }
    } catch (err) {
      console.error('checkAuth ', err);
      //  401 
      if (err.originalError?.response?.status === 401) {
        clearAuth();
      }
    } finally {
      setLoading(false);
    }
  }, [clearAuth]);

  // 
  useEffect(() => {
    checkAuth();
  }, [checkAuth]);

  // 
  const authContextValue = {
    isAuthenticated,
    user,
    token,
    loading,
    error,
    role,
    login,
    register,
    logout,
    clearError
  };

  return (
    <AuthContext.Provider value={authContextValue}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider; 