import React from 'react';
import { motion } from 'framer-motion';
import './ProgressBar.css';

// 
const STAGES = [
    { key: 'listening', label: '', icon: '' },
    { key: 'thinking', label: '', icon: '' },
    { key: 'executing', label: '', icon: '' },
    { key: 'completed', label: '', icon: '' }
];

/**
 * 
 * @param {Object} props - 
 * @param {string} props.currentStage -  ('listening', 'thinking', 'executing', 'completed', 'idle')
 * @param {boolean} props.visible - 
 * @param {string} props.className - CSS
 * @param {Object} props.customLabels - 
 */
const ProgressBar = ({
    currentStage = 'idle',
    visible = true,
    className = '',
    customLabels = {}
}) => {
    // idle
    if (!visible || currentStage === 'idle') {
        return null;
    }

    // 
    const getCurrentStageIndex = () => {
        const index = STAGES.findIndex(stage => stage.key === currentStage);
        return index >= 0 ? index : 0;
    };

    const currentStageIndex = getCurrentStageIndex();

    // 
    const getProgressPercentage = () => {
        if (currentStage === 'completed') {
            return 100;
        }
        return ((currentStageIndex + 1) / STAGES.length) * 100;
    };

    const progressPercentage = getProgressPercentage();

    return (
        <motion.div
            className={`progress-bar-container ${className}`}
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.3 }}
            data-testid="progress-bar"
        >
            {/*  */}
            <div className="progress-bar-track">
                {/*  */}
                <motion.div
                    className="progress-bar-fill"
                    data-testid="progress-bar-fill"
                    initial={{ width: 0 }}
                    animate={{ width: `${progressPercentage}%` }}
                    transition={{ duration: 0.5, ease: "easeInOut" }}
                />
            </div>

            {/*  */}
            <div className="progress-stages">
                {STAGES.map((stage, index) => {
                    const isActive = index <= currentStageIndex;
                    const isCurrent = index === currentStageIndex;
                    const isCompleted = index < currentStageIndex || currentStage === 'completed';

                    return (
                        <motion.div
                            key={stage.key}
                            className={`progress-stage ${isActive ? 'active' : ''} ${isCurrent ? 'current' : ''} ${isCompleted ? 'completed' : ''}`}
                            initial={{ scale: 0.8, opacity: 0.5 }}
                            animate={{
                                scale: isCurrent ? 1.1 : 1,
                                opacity: isActive ? 1 : 0.5
                            }}
                            transition={{ duration: 0.3 }}
                        >
                            {/*  */}
                            <div className="stage-icon">
                                {isCompleted ? '' : stage.icon}
                            </div>

                            {/*  */}
                            <div className="stage-label">
                                {customLabels[stage.key] || stage.label}
                            </div>

                            {/*  */}
                            {isCurrent && currentStage !== 'completed' && (
                                <motion.div
                                    className="stage-pulse"
                                    animate={{
                                        scale: [1, 1.2, 1],
                                        opacity: [0.7, 1, 0.7]
                                    }}
                                    transition={{
                                        duration: 1.5,
                                        repeat: Infinity,
                                        ease: "easeInOut"
                                    }}
                                />
                            )}
                        </motion.div>
                    );
                })}
            </div>

            {/*  */}
            <motion.div
                className="progress-description"
                key={currentStage}
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 0.3 }}
            >
                {currentStage === 'listening' && '...'}
                {currentStage === 'thinking' && '...'}
                {currentStage === 'executing' && '...'}
                {currentStage === 'completed' && ''}
            </motion.div>
        </motion.div>
    );
};

export default ProgressBar; 