import { useState, useCallback } from 'react';

/**
 * Hook
 * 
 * @returns {Object} 
 */
const useIntent = () => {
  const [intent, setIntent] = useState(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [error, setError] = useState(null);
  const [lastText, setLastText] = useState(''); // 
  const [lastConfirmAt, setLastConfirmAt] = useState(0); // 

  /**
   * 
   * @param {string} text - 
   * @param {string} originalQuery - 
   * @returns {string} : 'CONFIRM', 'RETRY', 'CANCEL'
   */
  const classifyIntent = useCallback((text, originalQuery = '') => {
    if (isProcessing) {
      console.log("", text);
      return null;
    }
    
    setIsProcessing(true);
    setError(null);
    
    try {
      // 
      if (!text) {
        console.log("");
        setIntent(null);
        setIsProcessing(false);
        return null;
      }

      // 
      const normalizedText = text.trim().toLowerCase();
      const normalizedOriginal = originalQuery.trim().toLowerCase();
      
      // 
      const now = Date.now();
      if (normalizedText === lastText && now - lastConfirmAt < 2000) {
        console.log("2:", text);
        setIsProcessing(false);
        return "IGNORE"; // 
      }
      
      // 
      if (normalizedText === normalizedOriginal) {
        console.log(":", normalizedText);
        setIsProcessing(false);
        
        // 
        if (now - lastConfirmAt > 5000) {
          console.log("5");
          setLastText(normalizedText);
          setLastConfirmAt(now);
          setIntent("CONFIRM");
          return "CONFIRM";
        }
        
        return "IGNORE";
      }
      
      // 
      const confirmPhrases = ['', '', '', '', '', '', '', '', '', '', '', '', 'ok', '', '', '', '', ''];
      const retryPhrases = ['', '', '', '', '', '', '', '', '', ''];
      const cancelPhrases = ['', '', '', 'forget it', '', '', '', '', '', ''];
      
      // 
      const hasConfirm = confirmPhrases.some(phrase => normalizedText.includes(phrase));
      const hasRetry = retryPhrases.some(phrase => normalizedText.includes(phrase));
      const hasCancel = cancelPhrases.some(phrase => normalizedText.includes(phrase));
      
      let result = null;
      
      // :  >  > 
      if (hasCancel) {
        console.log("");
        result = "CANCEL";
      } else if (hasRetry) {
        console.log("");
        result = "RETRY";
      } else if (hasConfirm) {
        console.log("");
        result = "CONFIRM";
      } else if (normalizedText.length > 15) {
        // 
        console.log("");
        result = "RETRY"; // /
      } else {
        // 
        console.log("");
        result = "CONFIRM";
      }
      
      // 
      setIntent(result);
      setLastText(normalizedText);
      setLastConfirmAt(now);
      setIsProcessing(false);
      
      return result;
    } catch (err) {
      console.error(":", err);
      setError(err.message);
      setIsProcessing(false);
      return null;
    }
  }, [isProcessing, lastText, lastConfirmAt]);

  /**
   * 
   */
  const resetIntent = useCallback(() => {
    setIntent(null);
    setIsProcessing(false);
    setError(null);
    setLastText('');
    setLastConfirmAt(0);
  }, []);

  return {
    intent,
    isProcessing,
    error,
    classifyIntent,
    resetIntent
  };
};

export default useIntent; 