// API

const interpret = async (transcript, sessionId, userId) => {
  try {
    console.log(`interpretsessionId: ${sessionId}`);
    const response = await api.post('/intent/interpret', {
      query: transcript,
      sessionId: sessionId,
      userId: userId,
    });
    console.log(`interpret:`, response.data);

    // sessionId
    if (response.data && response.data.sessionId) {
      console.log(`sessionId: ${response.data.sessionId}`);
    } else {
      console.warn(` : sessionId! :`, response.data);
    }

    return response.data; // Extract data from successful response
  } catch (error) {
    console.error('API call to interpret failed in function:', error);
    // Re-throw the processed error object from the interceptor
    throw error;
  }
};

const execute = async (toolId, params, sessionId, userId) => {
  try {
    // 
    if (!toolId) {
      throw new Error('ID');
    }
    if (!params || typeof params !== 'object') {
      throw new Error('');
    }

    // userId
    const userIdStr = userId ? String(userId) : null;

    console.log(`executesessionId: ${sessionId}`);

    // ExecuteRequest
    const requestData = {
      tool_id: toolId,
      params: params,
      sessionId: sessionId, // sessionIdID
    };

    // userIduser_id
    if (userIdStr) {
      requestData.user_id = userIdStr;
    }

    console.log("execute:", requestData);

    const response = await api.post('/execute', requestData);

    console.log("Execute API Response:", response);

    // sessionId
    if (response.data && response.data.sessionId) {
      console.log(`ExecutesessionId: ${response.data.sessionId}`);
    } else {
      console.warn(` : ExecutesessionId! :`, response.data);
    }

    return response.data;
  } catch (error) {
    console.error('API call to execute failed in function:', error);
    // Re-throw the processed error object from the interceptor
    throw error;
  }
};

const getItems = async (page = 1, pageSize = 10) => {
  try {
    console.log(`... : ${page}, : ${pageSize}`);
    const response = await api.get('/tools', {
      params: {
        page,
        page_size: pageSize
      }
    });
    console.log(":", response.data);

    // 
    return {
      items: response.data.tools || [],
      pagination: {
        current_page: response.data.current_page || page,
        total_pages: response.data.total_pages || 1,
        total_items: response.data.total_items || response.data.items?.length || 0,
        page_size: response.data.page_size || pageSize,
        has_next: response.data.has_next || false,
        has_prev: response.data.has_prev || false
      }
    };
  } catch (error) {
    console.error(':', error);
    throw error;
  }
};

