import React, { useState } from 'react';
import styled from 'styled-components';
import { useTheme } from '../../contexts/ThemeContext';
import ThemeToggle from '../../components/ThemeToggle';
import ThemeSettings from '../../components/ThemeSettings';
import StyleEditor from '../../components/StyleEditor';
import { Tabs } from 'antd-mobile';

// 
const SettingsContainer = styled.div`
  padding: 1.5rem;
  max-width: 1200px;
  margin: 0 auto;
  
  @media (max-width: 768px) {
    padding: 1rem;
  }
`;

// 
const Title = styled.h1`
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-color);
  display: flex;
  align-items: center;
  justify-content: center; /*  */
  gap: 0.75rem;
  text-align: center; /*  */
`;

//  - 
const SettingsCardContainer = styled.div`
  display: grid;
  gap: 1.5rem; /*  */
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  margin-bottom: 1.5rem; /*  */
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 1rem; /*  */
    margin-bottom: 1rem;
  }
`;

// 
const SettingsCard = styled.div`
  background-color: var(--surface);
  padding: 1.5rem;
  border-radius: var(--border-radius, 12px); /*  */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid var(--border-color);
  transition: all 0.2s ease;
  
  &:hover {
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.15);
  }
  
  /*  */
  h2 {
    font-size: 1.25rem;
    margin: 0 0 0.75rem 0;
    font-weight: 600;
    color: var(--text-color);
    display: flex;
    align-items: center;
    gap: 0.75rem;
    line-height: 1.4;
    text-align: left; /*  */
  }
  
  /*  */
  > p {
    margin: 0 0 1.25rem 0;
    color: var(--text-secondary);
    font-size: 0.9rem;
    line-height: 1.5;
    text-align: left; /*  */
  }
  
  /*  */
  hr {
    margin: 1.25rem 0;
    border: none;
    border-top: 1px solid var(--border-color);
  }
`;

// - 
const FullWidthCard = styled(SettingsCard)`
  grid-column: 1 / -1;
  margin-bottom: 1.5rem; /*  */
  
  &:last-child {
    margin-bottom: 0; /*  */
  }
  
  @media (max-width: 768px) {
    margin-bottom: 1rem;
  }
`;



const StyledTabs = styled(Tabs)`
  --active-line-color: var(--primary-color);
  --active-title-color: var(--primary-color);
  --title-font-size: 1rem;
  margin-bottom: 1.5rem;
  
  .adm-tabs-tab {
    color: var(--text-color) !important;
  }
  
  .adm-tabs-tab-active {
    color: var(--primary-color) !important;
  }
  
  /*  */
  [data-theme="dark"] & {
    .adm-tabs-tab {
      color: var(--dark-text) !important;
    }
    
    .adm-tabs-tab-active {
      color: var(--primary-color) !important;
    }
  }
`;

// 
const SettingRow = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 1rem 0;
  border-bottom: 1px solid var(--border-color);
  
  &:last-child {
    border-bottom: none;
    padding-bottom: 0;
  }
  
  &:first-child {
    padding-top: 0;
  }
  
  /*  */
  .label-section {
    flex: 1;
    margin-right: 1rem;
  }
  
  /*  */
  label {
    display: block;
    color: var(--text-color);
    font-weight: 500;
    font-size: 0.95rem;
    line-height: 1.4;
    margin-bottom: 0.25rem;
    text-align: left; /*  */
  }
  
  /*  */
  .description {
    color: var(--text-secondary);
    font-size: 0.85rem;
    line-height: 1.4;
    margin: 0;
    text-align: left; /*  */
  }
  
  /*  */
  .control-section {
    display: flex;
    align-items: center;
    flex-shrink: 0;
  }
  
  /*  */
  select {
    padding: 0.5rem 0.75rem;
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius, 8px);
    background-color: var(--surface);
    color: var(--text-color);
    font-size: 0.9rem;
    min-width: 120px;
    
    &:focus {
      outline: none;
      border-color: var(--primary-color);
      box-shadow: 0 0 0 2px rgba(79, 209, 197, 0.2);
    }
  }
`;

// 
const Settings = () => {
  const { theme } = useTheme();
  const [activeTab, setActiveTab] = useState('appearance');

  return (
    <SettingsContainer>
      <Title>
        <span aria-hidden="true"></span>
        
      </Title>

      <StyledTabs activeKey={activeTab} onChange={setActiveTab}>
        <Tabs.Tab title="" key="appearance">
          <SettingsCardContainer>
            {/*  */}
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>

              <SettingRow>
                <div className="label-section">
                  <label htmlFor="theme-toggle-button-instance"></label>
                  <div className="description">
                    {theme.isDark ? '' : ''}
                  </div>
                </div>
                <div className="control-section">
                  <ThemeToggle id="theme-toggle-button-instance" />
                </div>
              </SettingRow>
            </SettingsCard>

            {/*  */}
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>

              <SettingRow>
                <div className="label-section">
                  <label htmlFor="font-family-select"></label>
                  <div className="description">
                    
                  </div>
                </div>
                <div className="control-section">
                  <select id="font-family-select" defaultValue="system" aria-label="">
                    <option value="system"></option>
                    <option value="sans-serif">Sans-serif</option>
                    <option value="serif">Serif</option>
                    <option value="monospace"></option>
                  </select>
                </div>
              </SettingRow>

              <SettingRow>
                <div className="label-section">
                  <label htmlFor="font-size-select"></label>
                  <div className="description">
                    
                  </div>
                </div>
                <div className="control-section">
                  <select id="font-size-select" defaultValue="medium" aria-label="">
                    <option value="small"></option>
                    <option value="medium"></option>
                    <option value="large"></option>
                  </select>
                </div>
              </SettingRow>
            </SettingsCard>
          </SettingsCardContainer>

          {/*  -  */}
          <FullWidthCard>
            <h2>
              <span aria-hidden="true"></span>
              
            </h2>
            <p></p>
            <hr />
            <ThemeSettings />
          </FullWidthCard>

          {/*  -  */}
          <FullWidthCard>
            <h2>
              <span aria-hidden="true"></span>
              
            </h2>
            <p>CSS</p>
            <hr />
            <StyleEditor />
          </FullWidthCard>
        </Tabs.Tab>

        <Tabs.Tab title="" key="advanced">
          <SettingsCardContainer>
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>

              <SettingRow>
                <div className="label-section">
                  <label htmlFor="cache-setting"></label>
                  <div className="description">
                    
                  </div>
                </div>
                <div className="control-section">
                  <select id="cache-setting" defaultValue="auto">
                    <option value="auto"></option>
                    <option value="aggressive"></option>
                    <option value="minimal"></option>
                  </select>
                </div>
              </SettingRow>

              <SettingRow>
                <div className="label-section">
                  <label htmlFor="debug-mode"></label>
                  <div className="description">
                    
                  </div>
                </div>
                <div className="control-section">
                  <select id="debug-mode" defaultValue="disabled">
                    <option value="disabled"></option>
                    <option value="enabled"></option>
                  </select>
                </div>
              </SettingRow>
            </SettingsCard>
          </SettingsCardContainer>
        </Tabs.Tab>

        <Tabs.Tab title="" key="account">
          <SettingsCardContainer>
            <SettingsCard>
              <h2>
                <span aria-hidden="true"></span>
                
              </h2>
              <p></p>

              <SettingRow>
                <div className="label-section">
                  <label htmlFor="username-input"></label>
                  <div className="description">
                    
                  </div>
                </div>
                <div className="control-section">
                  <input
                    id="username-input"
                    type="text"
                    defaultValue=""
                    style={{
                      padding: '0.5rem 0.75rem',
                      border: '1px solid var(--border-color)',
                      borderRadius: '8px',
                      backgroundColor: 'var(--surface)',
                      color: 'var(--text-color)',
                      fontSize: '0.9rem',
                      minWidth: '120px'
                    }}
                  />
                </div>
              </SettingRow>

              <SettingRow>
                <div className="label-section">
                  <label htmlFor="privacy-setting"></label>
                  <div className="description">
                    
                  </div>
                </div>
                <div className="control-section">
                  <select id="privacy-setting" defaultValue="standard">
                    <option value="strict"></option>
                    <option value="standard"></option>
                    <option value="relaxed"></option>
                  </select>
                </div>
              </SettingRow>
            </SettingsCard>
          </SettingsCardContainer>
        </Tabs.Tab>
      </StyledTabs>
    </SettingsContainer>
  );
};

export default Settings; 