import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import { QuestionCircleOutlined, CheckOutlined, CloseOutlined, UndoOutlined, AudioOutlined } from '@ant-design/icons';
import useVoice from '../hooks/useVoice';
import useIntent from '../hooks/useIntent';
import { useTheme } from '../contexts/ThemeContext';
import useTTS from '../hooks/useTTS';

// 
const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: ${props => props.theme.dialogOverlay};
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
  
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
`;

// 
const ModalContent = styled.div`
  background-color: ${props => props.theme.dialogBackground};
  border-radius: 12px;
  width: 90%;
  max-width: 480px;
  padding: 24px;
  box-shadow: 0 4px 20px ${props => props.theme.shadowColor};
  position: relative;
  animation: slideIn 0.3s ease;
  
  @keyframes slideIn {
    from { transform: translateY(20px); opacity: 0; }
    to { transform: translateY(0); opacity: 1; }
  }
`;

// 
const ModalIcon = styled.div`
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: ${props => props.theme.primary}22;
  color: ${props => props.theme.primary};
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  margin: 0 auto 16px;
`;

// 
const ModalTitle = styled.h3`
  color: ${props => props.theme.text};
  text-align: center;
  margin-bottom: 16px;
  font-size: 18px;
`;

// 
const ModalText = styled.div`
  color: ${props => props.theme.text};
  text-align: center;
  margin-bottom: 24px;
  line-height: 1.6;
  font-size: 16px;
`;

// 
const ButtonGroup = styled.div`
  display: flex;
  justify-content: center;
  gap: 12px;
`;

// 
const Button = styled.button`
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: all 0.2s ease;
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 2px 8px ${props => props.theme.shadowColor};
  }
  
  &:active {
    transform: translateY(0);
  }
`;

// 
const ConfirmButton = styled(Button)`
  background-color: ${props => props.theme.primary};
  color: ${props => props.theme.buttonText};
  border: none;
`;

// 
const CancelButton = styled(Button)`
  background-color: transparent;
  color: ${props => props.theme.textSecondary};
  border: 1px solid ${props => props.theme.border};
`;

// 
const RetryButton = styled(Button)`
  background-color: ${props => props.theme.secondary};
  color: ${props => props.theme.buttonText};
  border: none;
`;

// 
const ListeningStatus = styled.div`
  color: ${props => props.theme.primary};
  text-align: center;
  font-size: 14px;
  margin-top: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  
  span {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: ${props => props.theme.primary};
    animation: pulse 1.5s infinite;
  }
  
  @keyframes pulse {
    0% { transform: scale(0.8); opacity: 0.5; }
    50% { transform: scale(1.2); opacity: 1; }
    100% { transform: scale(0.8); opacity: 0.5; }
  }
`;

/**
 * 
 * @param {Object} props - 
 * @param {boolean} props.isOpen - 
 * @param {string} props.confirmText - 
 * @param {Function} props.onConfirm - 
 * @param {Function} props.onRetry - 
 * @param {Function} props.onCancel - 
 * @param {boolean} props.useVoiceConfirmation - 
 * @param {boolean} props.isListening - 
 * @param {boolean} props.isTTSSpeaking - TTS
 * @param {Function} props.startSTTListening - 
 * @param {Function} props.stopSTTListening - 
 */
const ConfirmationModal = ({
  isOpen,
  confirmText,
  onConfirm,
  onRetry,
  onCancel,
  useVoiceConfirmation = true,
  isListening,
  isTTSSpeaking,
  startSTTListening,
  stopSTTListening,
  onTTSCompleted,
  voiceTranscript
}) => {
  const { theme } = useTheme();
  // useVoice hook
  const voiceHook = useVoice();
  const { classifyIntent } = useIntent();
  const { speak, cancel: cancelTTS } = useTTS();

  // hook
  const isSTTListening = isListening !== undefined ? isListening : voiceHook.isListening;
  const transcript = voiceTranscript || voiceHook.transcript; // transcript
  const startListening = startSTTListening || voiceHook.startListening;
  const stopListening = stopSTTListening || voiceHook.stopListening;

  const [isConfirmListening, setIsConfirmListening] = useState(false);
  const [showButtons, setShowButtons] = useState(false);
  const [ttsFinished, setTtsFinished] = useState(false);
  const ttsStartedRef = useRef(false); // ref
  const lastProcessedTranscript = useRef(''); // 

  // modal
  useEffect(() => {
    if (isOpen) {
      // Modal
      setIsConfirmListening(false);
      setShowButtons(false);
      setTtsFinished(false);
      ttsStartedRef.current = false; // TTS
      lastProcessedTranscript.current = ''; // 
    }
  }, [isOpen]);

  // 
  const handleStartVoiceListening = () => {
    console.log("ConfirmationModal: ");
    setIsConfirmListening(true);
    // 
    try {
      stopListening();
    } catch (e) {
      console.log(":", e);
    }

    // 
    setTimeout(() => {
      startListening();
    }, 100);

    // 10
    setTimeout(() => {
      if (isConfirmListening) {
        console.log("ConfirmationModal: ");
        stopListening();
        setIsConfirmListening(false);
      }
    }, 10000);
  };

  // 
  useEffect(() => {
    if (isOpen && confirmText && !ttsStartedRef.current) {
      console.log("ConfirmationModal: ");
      ttsStartedRef.current = true; // TTS
      setTtsFinished(false);
      setShowButtons(false); // 

      //  React  speak()
      console.log("ConfirmationModal: ...");

      speak(confirmText, 'zh-CN', 1, 1, () => {
        console.log("ConfirmationModal: TTSttsFinished=true");
        setTtsFinished(true);
        setShowButtons(true);
        console.log("ConfirmationModal: ");

        //  MainPage TTS 
        if (onTTSCompleted) {
          onTTSCompleted();
        }
      });

      //  TTS
      return () => {
        if (!isOpen) {
          cancelTTS();
        }
      };
    }

    if (!isOpen) {
      console.log("ConfirmationModal: TTS");
      cancelTTS();
      setIsConfirmListening(false);
      setShowButtons(false);
      setTtsFinished(false);
      ttsStartedRef.current = false; // TTS
      // 
      if (isSTTListening) {
        stopListening();
      }
    }
  }, [isOpen, confirmText, useVoiceConfirmation, speak, cancelTTS, isSTTListening, stopListening]);

  // TTS
  useEffect(() => {
    if (isOpen && confirmText && ttsStartedRef.current && !ttsFinished && !showButtons) {
      const safetyTimer = setTimeout(() => {
        console.log("ConfirmationModal: ");
        setTtsFinished(true);
        setShowButtons(true);
      }, 5000); // 5

      return () => clearTimeout(safetyTimer);
    }
  }, [isOpen, confirmText, ttsFinished, showButtons]);

  //  STT 
  useEffect(() => {
    console.log("ConfirmationModal: useEffect triggered with transcript:", transcript, "isOpen:", isOpen);

    // isConfirmListening
    if (transcript && transcript !== lastProcessedTranscript.current && isOpen) {
      console.log("ConfirmationModal: Received transcript for confirmation:", transcript);
      console.log("ConfirmationModal: isConfirmListening:", isConfirmListening);

      // 
      lastProcessedTranscript.current = transcript;

      const intent = classifyIntent(transcript);
      console.log("ConfirmationModal: Classified intent:", intent);

      // 
      if (isConfirmListening) {
        stopListening();
        setIsConfirmListening(false);
      }

      switch (intent) {
        case 'CONFIRM':
          console.log("ConfirmationModal: Executing CONFIRM action");
          onConfirm();
          break;
        case 'RETRY':
          console.log("ConfirmationModal: Executing RETRY action");
          onRetry();
          break;
        case 'CANCEL':
          console.log("ConfirmationModal: Executing CANCEL action");
          onCancel();
          break;
        case 'IGNORE':
          console.log("ConfirmationModal: Ignoring recognized text");
          setShowButtons(true);
          break;
        default:
          console.log("ConfirmationModal: Unclear intent or unrelated speech, showing buttons");
          setShowButtons(true);
          break;
      }
    } else {
      console.log("ConfirmationModal: useEffect condition not met - transcript:", transcript, "lastProcessed:", lastProcessedTranscript.current, "isOpen:", isOpen);
    }
  }, [transcript, isConfirmListening, isOpen, classifyIntent, stopListening, onConfirm, onRetry, onCancel]);

  // 
  const handleConfirm = () => {
    cancelTTS();
    if (isConfirmListening) {
      stopListening();
    }
    onConfirm();
  };

  const handleRetry = () => {
    cancelTTS();
    if (isConfirmListening) {
      stopListening();
    }
    onRetry();
  };

  const handleCancel = () => {
    cancelTTS();
    if (isConfirmListening) {
      stopListening();
    }
    onCancel();
  };

  // 

  if (!isOpen) {
    return null;
  }

  return (
    <ModalOverlay theme={theme}>
      <ModalContent theme={theme}>
        <ModalIcon theme={theme}>
          <QuestionCircleOutlined />
        </ModalIcon>
        <ModalTitle theme={theme}></ModalTitle>
        <ModalText theme={theme}>
          {confirmText}
        </ModalText>
        {/*  */}

        {isConfirmListening && (
          <ListeningStatus theme={theme}>
            <span></span> ...
          </ListeningStatus>
        )}

        {transcript && !isConfirmListening && (
          <ModalText theme={theme} style={{ fontStyle: 'italic', marginTop: '16px' }}>
            : {transcript}
          </ModalText>
        )}

        {showButtons && !isConfirmListening && (
          <ButtonGroup>
            <ConfirmButton theme={theme} onClick={handleConfirm}>
              <CheckOutlined /> 
            </ConfirmButton>
            <RetryButton theme={theme} onClick={handleRetry}>
              <UndoOutlined /> 
            </RetryButton>
            <CancelButton theme={theme} onClick={handleCancel}>
              <CloseOutlined /> 
            </CancelButton>
          </ButtonGroup>
        )}


      </ModalContent>
    </ModalOverlay>
  );
};

export default ConfirmationModal; 