import React, { useContext } from 'react';
import { Switch } from 'antd';
import styled from 'styled-components';
import { ThemeContext } from '../contexts/ThemeContext';

// 
const ToggleContainer = styled.div`
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 120px; /*  */
  
  @media (max-width: 768px) {
    min-width: 100px; /*  */
    gap: 0.25rem;
  }
`;

// 
const ThemeIcon = styled.span`
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  flex-shrink: 0; /*  */
  
  @media (max-width: 768px) {
    font-size: 14px;
  }
`;

// Switch
const StyledSwitch = styled(Switch)`
  /* Switch */
  min-width: 44px !important;
  height: 22px !important;
  flex-shrink: 0; /*  */
  
  /*  */
  @media (max-width: 768px) {
    min-width: 40px !important;
    height: 20px !important;
    
    /*  */
    .ant-switch-inner {
      font-size: 10px !important;
      line-height: 18px !important;
      padding: 0 4px !important;
    }
    
    /*  */
    .ant-switch-handle {
      width: 16px !important;
      height: 16px !important;
      top: 2px !important;
    }
    
    .ant-switch-handle::before {
      border-radius: 8px !important;
    }
  }
  
  /*  */
  &.ant-switch-checked {
    background-color: var(--primary-color) !important;
  }
  
  /*  */
  .ant-switch-inner {
    color: white !important;
    font-weight: 500;
  }
  
  /*  -  */
  &, &.ant-switch-checked, &:hover, &:focus, &:active {
    height: 22px !important;
    
    @media (max-width: 768px) {
      height: 20px !important;
    }
  }
`;

const ThemeToggle = ({ themeOverride = null, toggleThemeOverride = null, id = null }) => {
  // 
  // 
  const contextValue = useContext(ThemeContext);
  const theme = themeOverride || contextValue?.theme;
  const toggleTheme = toggleThemeOverride || contextValue?.toggleTheme;

  const isDark = theme?.isDark;

  return (
    <ToggleContainer>
      <ThemeIcon aria-hidden="true">
        {isDark ? '' : ''}
      </ThemeIcon>
      <StyledSwitch
        id={id}
        checked={isDark}
        onChange={toggleTheme}
        checkedChildren=""
        unCheckedChildren=""
        aria-label=""
        aria-describedby={id ? `${id}-description` : undefined}
        role="switch"
        aria-checked={isDark}
      />
      {id && (
        <span id={`${id}-description`} className="sr-only">
          {isDark ? '' : ''}{isDark ? '' : ''}
        </span>
      )}
    </ToggleContainer>
  );
};

export default ThemeToggle; 